
.alert {
    display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(184, 242, 7, 0.91);
  top:0;
  justify-content: center;
}  
.alert span{
  position: relative;
  font-size: 24px;
  top: 20%;
  padding: 22px;
}
@media only screen and (orientation:portrait){
  .alert {
    display: flex;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.board{
  background-color: rgb(7, 99, 36);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-top: 25px;
  box-sizing: border-box;
  user-select: none;
}

.check-button{
  position: absolute;
  top: 50px;
      left: 40px;
    border-radius: 50%;
    height: 80px;
    width: 80px;
    background-color: rgba(50, 255, 115, 0.68);
    -webkit-appearance: none;
    -moz-appearance: none;
    border: none;
    box-shadow: inset 0 0 5px 5px rgba(50, 255, 115, 0.68);
    user-select: none;
}
button:focus {
    outline: none;
    outline-offset: -4px;
}
button:active {
    transform: scale(0.98);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
